import { type FC } from "react";
import { useTranslation } from "react-i18next";
import HexImage from "@/components/ui/HexImage";
const office_image =
  "";
const OfficeImage: FC = () => {
  const { t } = useTranslation();

  return (
    <HexImage
      hexData={office_image}
      imageType="png"
      alt={t("contact.office.caption")}
      className="w-full h-64 md:h-80 rounded-lg object-cover"
    />
  );
};
export default OfficeImage;
